:root {
  --fg-primary: 12vh;
  --fg-secondary: 3vh;
  --fg-date: 8vh;
  --fg-list: 2vh;
  --icon: 3vh;
  --fg: #000;
  --sfg: #494949;
  --accent: #57a0d9;
  --background: #f5f5f5;
  --cards: #e4e6e6;
  --imgcol: linear-gradient( rgba(255, 255, 255, .7), rgba(255, 255, 255, .7) );
}

.darktheme {
  --accent: #57a0d9;
  --background: #19171a;
  --cards: #201e21;
  --fg: #d8dee9;
  --sfg: #2c292e;
  --imgcol: linear-gradient( rgba(0, 0, 0, .85), rgba(0, 0, 0, .85) );
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
}

textarea {
