}

* {
  transition: all .2s ease-in-out;
}

body {
  background-image: url("theme_winter_1.37f5d5b0.jpg");
}

.darktheme {
  background-image: url("theme_winter_3.72c8c210.jpg");
  background-repeat: no-repeat;
}

#snow_light {
  height: 100%;
  pointer-events: none;
  width: 100%;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.notransition {
  -o-transition: none;
  transition: none;
}

.withImageBackground {
  background-size: cover;
}

#themeButton {
  color: var(--fg);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 2em 2em 0 0;
  position: absolute;
  top: 0;
  right: 0;
}

#themeIcon {
  width: 25px;
  height: 25px;
}

.container {
  width: 145vh;
  height: 85vh;
  grid-gap: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: grid;
}

.card {
  background-color: var(--cards);
  border-radius: 5px;
  box-shadow: 0 5px 7px rgba(0, 0, 0, .35);
}

.card:hover {
  transform: translateY(-.2rem);
  box-shadow: 0 10px 10px rgba(0, 0, 0, .35);
}

.timeBlock {
  flex-direction: column;
  grid-area: 1 / 1 / span 2 / span 2;
  justify-content: center;
  align-items: center;
  margin-right: 30px;
  display: flex;
}

.weatherBlock {
  flex-direction: column;
  grid-area: 1 / 3 / span 2 / span 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.date {
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.weather {
  justify-content: center;
  align-items: center;
  display: flex;
}

.clock {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.icon_winter {
  position: absolute;
}

#hour, #separator, #minutes {
  font-size: var(--fg-primary);
  color: var(--fg);
  font-weight: bolder;
}

#month, #day {
  font-size: var(--fg-date);
  color: var(--fg);
  font-weight: bold;
}

#day {
  margin-left: 1.5rem;
}

#greetings {
  font-size: var(--fg-secondary);
  color: var(--fg);
}

.weatherIcon img {
  width: 70px;
  height: 70px;
}

.weatherValue p {
  font-size: var(--fg-secondary);
  color: var(--fg);
  margin-left: 15px;
  font-weight: bolder;
}

.weatherDescription p {
  font-size: var(--fg-secondary);
  color: var(--fg);
  margin-left: 15px;
}

.reduceGap {
  grid-gap: 2rem;
}

.removeGap {
  grid-gap: 0;
}

.linksBlock {
  grid-gap: 1.5rem;
  grid-area: 3 / 1 / span 2 / span 4;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.my-app__title {
  color: var(--fg);
  font-size: 6vh;
  font-weight: bolder;
}

.linksBlockLeft {
  grid-area: 1 / 1 / span 2 / span 2;
}

.linksBlockRight {
  grid-area: 1 / 3 / span 2 / span 2;
}

.buttonsContainer {
  width: 100%;
  height: 100%;
  grid-gap: 30px;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  padding: 20px;
  display: grid;
}

.button {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.buttonIcon {
  width: var(--icon);
  height: var(--icon);
  color: var(--fg);
}

.button__1 {
  border: 50px solid;
  border-image: url("winter_border_1.7880e439.png") 600;
  grid-area: 1 / 1;
}

.button__2 {
  border: 20px solid;
  border-image: url("red_border.2fa2ca5e.png") 80;
  grid-area: 1 / 2;
}

.button__3 {
  border: 50px solid;
  border-image: url("winter_border_1.7880e439.png") 600;
  grid-area: 2 / 1;
}

.button__4 {
  border: 50px solid;
  border-image: url("winter_border_1.7880e439.png") 600;
  grid-area: 2 / 2;
}

.button__5 {
  border: 20px solid;
  border-image: url("red_border.2fa2ca5e.png") 80;
  grid-area: 1 / 3;
}

.button__6 {
  grid-area: 2 / 3;
}

.button:hover {
  background-color: var(--accent);
}

.button:hover svg {
  stroke: var(--sfg);
}

.listsContainer {
  width: 100%;
  height: 100%;
  grid-gap: 3rem;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  padding: .4rem;
  display: grid;
}

.list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.list__1 {
  border: 60px solid;
  border-image: url("winter_border_1.7880e439.png") 500;
  border-radius: 50px;
  grid-area: 1 / 1 / span 2;
}

.list__2 {
  border: 60px solid;
  border-image: url("winter_border_1.7880e439.png") 500;
  grid-area: 1 / 2 / span 2;
}

.listIcon {
  color: var(--fg);
  width: var(--icon);
  height: var(--icon);
  margin-bottom: 2vh;
}

.listItem {
  font-size: var(--fg-list);
  color: var(--fg);
  text-align: center;
  width: 80%;
  border-radius: 5px;
  margin-top: 1vh;
  padding: 8px 12px;
  font-weight: bold;
  text-decoration: none;
}

.listItem:hover {
  background-color: var(--accent);
  color: var(--sfg);
}

@media only screen and (max-width: 68.75em) {
  .container {
    grid-gap: 20px;
    padding: 40px;
  }

  .timeBlock {
    grid-area: 1 / 1 / span 2 / span 4;
  }

  .weatherBlock {
    display: none;
  }

  #greetings {
    font-size: var(--fg-secondary);
  }

  .linksBlockRight {
    display: none;
  }

  .linksBlockLeft {
    grid-column: 1 / span 4;
  }

  .button {
    grid-gap: 3vw;
    padding-left: 100px;
    padding-right: 100px;
  }
}

